body{
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Pompiere', cursive;
  font-size: 1.3rem;
}
.pointer{
  cursor: pointer;
}
/* ====== LOGIN ============== */
.login-container{
  width: 100%;
  height: 100%;
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  background-image: url('./assets/img/login-fondo.jpg');
  position: fixed;
}
.form-container{
  position: relative;
  margin: auto;
  top: 25%;
}
.form-container span.textError {
  background-color: #dc3545;
  display: block;
  position: relative;
  top: -3px;
  z-index: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  color: #fff;
  padding: 2px 10px;
  font-size: 16px;
  border: 1px solid #c10f21;
}
form {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 10px;
  border-radius: 10px;
}
.form-container .form-control{
  z-index: 1;
  position: relative;
  border-color: #989494;
}

.toast{
  position: absolute; 
  top: 20px; 
  right: 20px;
  min-width: 250px;
}

/* ============================== */